export const BEHAVIOR_NAME = 'TABLES_DATA_BEHAVIOR';
export const TABLES_DATA_PROPERTY = 'tablesDataProperty';
export const EXTENDABLE_TABLES_DATA_PROPERTY = 'extendableTablesDataProperty';
export const TABLES_DATA_IN_SCOPE_PROPERTY = 'tablesDataInScopeProperty';
export const TABLES_DATA_OUT_OF_SCOPE_PROPERTY = 'tablesDataOutOfScopeProperty';
export const TABLES_DATA_OPTIONS_PROPERTY = 'tablesDataOptions';

export const TABLES_DATA_LOADING_PROPERTY = loadingPropertyName(
	TABLES_DATA_PROPERTY
);
export const EXTENDABLE_TABLES_DATA_LOADING_PROPERTY = loadingPropertyName(
	EXTENDABLE_TABLES_DATA_PROPERTY
);

export function loadingPropertyName(propName) {
	return `${propName}Loading`;
}

// Misc variables
export const EXTENDABLE_FIELD = 'only_extendable';
export const DEFAULT_QUERY_PARAMS = {
	order_by: 'label',
	only_in_scope: true,
	only_out_of_scope: false,

	// The below properties are expected to possibly be filled in by the behavior
	sysparm_transaction_scope: undefined,
	[EXTENDABLE_FIELD]: undefined
};

// Action names
export const TABLES_DATA_BEHAVIOR__FETCH_ALL = `${BEHAVIOR_NAME}#FETCH_ALL`;
export const TABLES_DATA_BEHAVIOR__FETCH_EXTENDABLE = `${BEHAVIOR_NAME}#FETCH_EXTENDABLE`;
export const TABLES_DATA_BEHAVIOR__FETCH_TABLES = `${BEHAVIOR_NAME}#FETCH_TABLES`;
export const TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES = `${BEHAVIOR_NAME}#FETCH_SCOPE_TABLES`;
export const TABLES_DATA_BEHAVIOR__FETCH_TABLES_STARTED = `${BEHAVIOR_NAME}#FETCH_TABLES_STARTED`;
export const TABLES_DATA_BEHAVIOR__FETCH_TABLES_SUCCEEDED = `${BEHAVIOR_NAME}#FETCH_TABLES_SUCCEEDED`;
export const TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES_SUCCEEDED = `${BEHAVIOR_NAME}#FETCH_SCOPE_TABLES_SUCCEEDED`;
export const TABLES_DATA_BEHAVIOR__FETCH_TABLES_FAILED = `${BEHAVIOR_NAME}#FETCH_TABLES_FAILED`;
