import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import { t } from 'sn-translate';

import {
	BEHAVIOR_NAME,
	RUN_TEMPLATE,
	RUN_TEMPLATE_WARNING,
	RUN_TEMPLATE_FAILURE,
	RUN_TEMPLATE_SUCCESS,
	INITIALIZE_TEMPLATE,
	INIT_TEMPLATE_STARTED,
	INIT_TEMPLATE_SUCCEEDED,
	INIT_TEMPLATE_FAILED,
	TEMPLATE_STATUS_TIMEOUT,
	DEBOUNCE_TEMPLATE_STATUS,
	FETCH_TEMPLATE_STATUS,
	FETCH_TEMPLATE_STATUS_STARTED,
	FETCH_TEMPLATE_STATUS_SUCCEEDED,
	FETCH_TEMPLATE_STATUS_FAILED,
	TEMPLATE_INSTANCE_RW_CHANNEL,
	SUBSCRIBE_TO_RW,
	SUBSCRIPTION_STARTED,
	SUBSCRIPTION_SUCCEEDED,
	SUBSCRIPTION_FAILED,
	SUBSCRIPTION_UNSUBSCRIBED,
	TEMPLATE_INSTANCE_CHANGED,
	CLEAN_UP
} from './constants';

/**
 * Handles formatting and sending the "Run Template" REST request.
 */
export default {
	name: BEHAVIOR_NAME,

	initialState: {
		templateInstanceId: null,
		timeoutId: null,
		inProgress: false
	},

	actionHandlers: {
		[actionTypes.COMPONENT_DISCONNECTED]: ({ state, dispatch }) => {
			dispatch(SUBSCRIBE_TO_RW, {
				filter: btoa(`sys_id=${state.templateInstanceId}`).replace(/=/g, '-'), // Idiosyncracy of ui-effect-amb, will be abstracted away in a future release
				subscribe: false
			});
		},
		// Some user input triggers the start of a template
		[RUN_TEMPLATE]: ({ action, dispatch }) => {
			const {
				template_sys_id,
				parent_template_instance_id = undefined,
				sysparm_transaction_scope = undefined,
				variables = {}
			} = action.payload;

			// Build request body
			const templateRequest = {
				template_sys_id,
				...(parent_template_instance_id && { parent_template_instance_id }),
				variables
			};

			// Kick-off the template runner
			dispatch(INITIALIZE_TEMPLATE, {
				templateRequest,
				...(sysparm_transaction_scope && { sysparm_transaction_scope })
			});
		},

		// Sends the request to the template runner endpoint to attempt to start a template
		[INITIALIZE_TEMPLATE]: createHttpEffect('/api/now/templates', {
			method: 'POST',
			startActionType: INIT_TEMPLATE_STARTED,
			successActionType: INIT_TEMPLATE_SUCCEEDED,
			errorActionType: INIT_TEMPLATE_FAILED, // A "warning" failure if you will
			batch: false,
			dataParam: 'templateRequest',
			queryParams: ['sysparm_transaction_scope']
		}),

		// Template runner endpoint returns success with the sysId of the template instance that is initialized
		[INIT_TEMPLATE_SUCCEEDED]: ({
			action: {
				payload: { result: templateInstanceId }
			},
			updateState,
			dispatch
		}) => {
			// Save it to the behavior
			updateState({
				path: `behaviors.${BEHAVIOR_NAME}.templateInstanceId`,
				value: templateInstanceId,
				operation: 'set'
			});

			// And set `inProgress`
			updateState({
				path: `behaviors.${BEHAVIOR_NAME}.inProgress`,
				value: true,
				operation: 'set'
			});

			// Now that the instance record has been created, subscribe to changes to that record
			dispatch(SUBSCRIBE_TO_RW, {
				filter: btoa(`sys_id=${templateInstanceId}`).replace(/=/g, '-'), // Idiosyncracy of ui-effect-amb, will be abstracted away in a future release
				subscribe: true
			});
		},

		// Subscribe to record-watcher AMB channel for changes to the initialized template instance
		[SUBSCRIBE_TO_RW]: createAmbSubscriptionEffect(
			TEMPLATE_INSTANCE_RW_CHANNEL,
			{
				subscribeStartedActionType: SUBSCRIPTION_STARTED,
				subscribeSucceededActionType: SUBSCRIPTION_SUCCEEDED,
				subscribeFailedActionType: SUBSCRIPTION_FAILED,
				unsubscribeSucceededActionType: SUBSCRIPTION_UNSUBSCRIBED,
				messageReceivedActionType: TEMPLATE_INSTANCE_CHANGED
			}
		),

		// It takes a moment to subscribe, the template instance could have already finished by now
		[SUBSCRIPTION_SUCCEEDED]: ({ dispatch }) => {
			dispatch(DEBOUNCE_TEMPLATE_STATUS);
		},

		[SUBSCRIPTION_FAILED]: ({ dispatch }) => {
			dispatch(RUN_TEMPLATE_FAILURE, {
				// Failure here because instance was successfully created
				message: t('Connection Failure'),
				detail: t('Subscription to record-watcher AMB channel failed')
			});
		},

		// Check the status of that instance to see if it has (our backend can be pretty snappy)
		[DEBOUNCE_TEMPLATE_STATUS]: {
			modifier: { name: 'debounce', delay: 500 }, // A half second debounce to give it plenty of leeway
			effect: ({
				state: {
					behaviors: {
						[BEHAVIOR_NAME]: { templateInstanceId }
					}
				},
				dispatch
			}) => {
				dispatch(FETCH_TEMPLATE_STATUS, {
					template_instance_id: templateInstanceId
				});
			}
		},

		[FETCH_TEMPLATE_STATUS]: createHttpEffect('/api/now/templates/status', {
			method: 'GET',
			startActionType: FETCH_TEMPLATE_STATUS_STARTED,
			successActionType: FETCH_TEMPLATE_STATUS_SUCCEEDED,
			errorActionType: FETCH_TEMPLATE_STATUS_FAILED,
			batch: false,
			queryParams: ['template_instance_id']
		}),

		// One of four 4 potential statuses could be returned
		[FETCH_TEMPLATE_STATUS_SUCCEEDED]: ({
			state: {
				behaviors: {
					[BEHAVIOR_NAME]: { templateInstanceId, inProgress }
				}
			},
			action: {
				payload: { result }
			},
			dispatch,
			updateState
		}) => {
			switch (result.status) {
				case 'COMPLETE':
					dispatch(RUN_TEMPLATE_SUCCESS, {
						result: { ...result, templateInstanceId }
					});
					break;
				case 'PENDING':
				case 'RUNNING': {
					// If it's PENDING or RUNNING then create a timeout to allow the record-watcher time to hear changes
					const timeoutId = setTimeout(() => {
						dispatch(RUN_TEMPLATE_FAILURE, {
							message: t('Template Execution Timeout'),
							detail: t(
								'sys_app_template_instance {0} timed out',
								templateInstanceId
							)
						});
					}, TEMPLATE_STATUS_TIMEOUT);

					// Save the id to state so it can be cleared on clean up
					updateState({
						path: `behaviors.${BEHAVIOR_NAME}.timeoutId`,
						value: timeoutId,
						operation: 'set'
					});
					break;
				}
				case 'FAILURE':
					// If somehow multiple FAILURE responses are heard, just dispatch the first time
					inProgress &&
						dispatch(RUN_TEMPLATE_FAILURE, {
							message: result.outputs[0].value.message, // TODO: This might change to just an `error` key
							detail: result.outputs[0].value.detail
						});
					break;
				default:
					dispatch(RUN_TEMPLATE_FAILURE, {
						message: t('Unknown Template Error'),
						detail: t('Unexpected template instance state')
					});
					break;
			}
		},

		[FETCH_TEMPLATE_STATUS_FAILED]: ({ dispatch }) => {
			// Treat as critical since template instance has been created
			dispatch(RUN_TEMPLATE_FAILURE, {
				message: t('Connection Failure'),
				detail: t('Request to template status endpoint failed')
			});
		},

		[TEMPLATE_INSTANCE_CHANGED]: ({
			state: {
				behaviors: {
					[BEHAVIOR_NAME]: { templateInstanceId }
				}
			},
			action: {
				payload: { data }
			},
			dispatch
		}) => {
			const {
				operation,
				record: { state: instanceState } // `record` key of `data` gives you the entire record, `state` is the field we care about
			} = data;

			// If the instance record somehow gets deleted here, whoops something real bad happened
			if (operation === 'delete') {
				dispatch(RUN_TEMPLATE_FAILURE, {
					message: t('Critical Failure'),
					detail: t(
						'sys_app_template_instance {0} was deleted',
						templateInstanceId
					)
				});
			}

			// Otherwise...
			if (
				operation === 'update' && // Here, we only care about updates to this record...
				(instanceState.value === 'complete' || instanceState.value === 'error') // Specifically when the instance's `state` changes to `complete` or `error`
			) {
				// Instead of mucking around with trying to shape the record-watcher message payload, just hit the status endpoint again
				dispatch(FETCH_TEMPLATE_STATUS, {
					template_instance_id: templateInstanceId
				});
			}
		},

		// If the original request to the template runner fails, nothing has been created yet. Therefore the inputs collected in the client
		// can still be sent again as a re-attempt that running the template. The cases for this failure are if the template doesn't exist
		// or if the template is deactivated. Something that can conceivably be fixed in the background
		[INIT_TEMPLATE_FAILED]: ({
			action: {
				payload: {
					data: {
						error: { message, detail }
					}
				}
			},
			dispatch
		}) => {
			dispatch(RUN_TEMPLATE_WARNING, { message, detail });
		},

		// Whether the runner was a success, warning, critical failure, clean-up request tracking
		[RUN_TEMPLATE_SUCCESS]: ({ dispatch }) => {
			dispatch(CLEAN_UP);
		},
		[RUN_TEMPLATE_WARNING]: ({ dispatch }) => {
			dispatch(CLEAN_UP);
		},
		[RUN_TEMPLATE_FAILURE]: ({ dispatch }) => {
			dispatch(CLEAN_UP);
		},
		[CLEAN_UP]: ({
			state: {
				behaviors: {
					[BEHAVIOR_NAME]: { templateInstanceId, timeoutId }
				}
			},
			dispatch,
			updateState
		}) => {
			// If there's a template instance started
			if (templateInstanceId) {
				// Unsubscribe from record-watcher
				dispatch(SUBSCRIBE_TO_RW, {
					filter: btoa(`sys_id=${templateInstanceId}`).replace(/=/g, '-'),
					subscribe: false
				});
				// Remove the sysId from state
				updateState({
					path: `behaviors.${BEHAVIOR_NAME}.templateInstanceId`,
					value: null,
					operation: 'set'
				});
			}

			// If there's a timeout
			if (timeoutId) {
				// Clear it
				clearTimeout(timeoutId);
				// Remove its id from state
				updateState({
					path: `behaviors.${BEHAVIOR_NAME}.timeoutId`,
					value: null,
					operation: 'set'
				});
			}

			// Reset `inProgress`
			updateState({
				path: `behaviors.${BEHAVIOR_NAME}.inProgress`,
				value: false,
				operation: 'set'
			});
		}
	}
};
