import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-stepper';
import {
	SN_GW__CONTINUE,
	SN_GW__CONTEXT_SET,
	SN_GW__BACK,
	SN_GW_PAGE_CHANGED
} from './constants';
import tablesDataBehavior from '../../behaviors/tables-data';
import rolesDataBehavior from '../../behaviors/roles-data';
import userDataBehavior from '../../behaviors/user-data';
import styles from './sn-guided-wizard.scss';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

createCustomElement('sn-guided-wizard', {
	view: ({ properties: { wizardContext, stepperItems } }) => {
		const stepperItemsLength = stepperItems.length;
		const currentPage = wizardContext.meta && wizardContext.meta.currentPage;
		return currentPage ? (
			<Fragment>
				<div
					class={{
						'sn-gw-stepper-container': true,
						[`items-${stepperItemsLength}`]: stepperItemsLength > 0
					}}>
					{stepperItems.length === 0 ? (
						<div />
					) : (
						<now-stepper items={stepperItems} manageSelectedItem={true} />
					)}
				</div>
				<slot />
			</Fragment>
		) : null;
	},
	properties: {
		flowDefinition: {
			default: {},
			selectable: true
		},
		wizardContext: {
			default: {
				meta: {
					currentPage: null
				}
			},
			selectable: true
		},
		stepperItems: { default: [] },
		initialContext: {},
		name: { default: '', reflect: true }
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: async ({ dispatch, state }) => {
			const {
				properties: {
					flowDefinition: { startPage },
					initialContext
				}
			} = state;
			dispatch(SN_GW__CONTEXT_SET, {
				meta: { currentPage: startPage },
				...initialContext
			});
		},
		[SN_GW__CONTEXT_SET]: ({
			state: {
				properties: { wizardContext }
			},
			action: { payload },
			updateProperties
		}) => {
			const updatedData = {
				...wizardContext,
				...payload
			};
			updateProperties({
				wizardContext: { ...updatedData }
			});
		},
		[SN_GW__CONTINUE]: ({ updateProperties, state, action, dispatch }) => {
			const {
				properties: { flowDefinition, wizardContext }
			} = state;
			const {
				meta: { currentPage }
			} = wizardContext;

			if (!flowDefinition) {
				//TODO better message
				console.error(
					'<sn-guided-wizard/> Template Wizard must be given a flow definition. See documentation here: doclink'
				);
				return;
			}

			const updatedData = {
				...wizardContext,
				...action.payload
			};

			const nextPage = flowDefinition.pages[currentPage].continue(updatedData);

			updatedData.meta = {
				...wizardContext.meta,
				currentPage: nextPage
			};

			updateProperties({
				wizardContext: { ...updatedData }
			});

			dispatch(SN_GW_PAGE_CHANGED, {
				currentPage: nextPage,
				previousPage: currentPage
			});
		},
		[SN_GW__BACK]: ({ updateProperties, state, action, dispatch }) => {
			const {
				properties: { flowDefinition, wizardContext }
			} = state;
			const {
				meta: { currentPage }
			} = wizardContext;

			if (!flowDefinition) {
				//TODO better message
				console.error(
					'<sn-guided-wizard/> Template Wizard must be given a flow definition. See documentation here: doclink'
				);
				return;
			}

			const updatedData = {
				...wizardContext,
				...action.payload
			};

			const nextPage = flowDefinition.pages[currentPage].previous(updatedData);

			updatedData.meta = {
				...wizardContext.meta,
				currentPage: nextPage
			};

			updateProperties({
				wizardContext: { ...updatedData }
			});

			dispatch(SN_GW_PAGE_CHANGED, {
				currentPage: nextPage,
				previousPage: currentPage
			});
		}
	},
	dispatches: {
		// Dispatched when wizard is complete to send action to parent app
		'SN_GUIDED_WIZARD#COMPLETE': {},

		// Dispatched when wizard is canceled to send action to parent app
		'SN_GUIDED_WIZARD#CANCEL': {},

		// Dispatched object with currentPage and previousPage
		'SN_GUIDED_WIZARD#PAGE_CHANGED': {}
	},
	behaviors: [
		{
			behavior: tablesDataBehavior.behavior
		},
		{
			behavior: rolesDataBehavior.behavior
		},
		{
			behavior: userDataBehavior.behavior
		}
	],
	styles
});
