/**
 * VALIDATION_BEHAVIOR
 *
 * A simple implementation of a page-level validation that relies upon Design System form
 * control components' dispatching of the `<COMPONENT>#INVALID_SET` action.
 *
 * This event is dispatched on each blur of primitive form components (and on each change
 * of the `value` property for the `now-input`) with a payload that contains the `name`
 * of the field passed into the component and a `value` boolean that corresponds to whether
 * the component is `invalid`.
 */

export const BEHAVIOR_NAME = 'VALIDATION_BEHAVIOR';

/**
 * createValidationStateEffect - generates an effect that relies on closure to determine whether
 * a `<COMPONENT>#INVALID_SET` action is for a field that is relevant to the behavior.
 */

export const createValidationStateEffect = fields => ({
	action: {
		payload: { name, value }
	},
	updateState
}) => {
	if (!fields.includes(name)) {
		return;
	}
	updateState({
		path: `behaviors.${BEHAVIOR_NAME}.${name}`,
		value: !value,
		operation: 'set'
	});
};

/**
 * createValidationBehavior - returns a validation behavior that will listen and react to
 * `<COMPONENT>#INVALID_SET` actions. The keys on the behavior's `initialState` map directly
 * to the array of strings passed in.
 *
 * This behavior relies on the Design System components to determine the valid state of a
 * field.
 */

export const createValidationBehavior = fields => {
	const fieldValidStates = fields.reduce((fieldsState, field) => {
		if (typeof field !== 'string') {
			throw new TypeError('Field must be a string');
		}
		fieldsState[field] = false;
		return fieldsState;
	}, {});

	const validationEffect = createValidationStateEffect(fields);

	return {
		name: BEHAVIOR_NAME,
		initialState: {
			...fieldValidStates
		},
		actionHandlers: {
			['NOW_INPUT#INVALID_SET']: validationEffect,
			['NOW_CHECKBOX#INVALID_SET']: validationEffect,
			['NOW_TEXTAREA#INVALID_SET']: validationEffect,
			['NOW_INPUT_URL#INVALID_SET']: validationEffect,
			['NOW_INPUT_PHONE#INVALID_SET']: validationEffect,
			['NOW_INPUT_PASSWORD#INVALID_SET']: validationEffect,
			['NOW_SELECT#INVALID_SET']: validationEffect,
			['NOW_TYPEAHEAD#INVALID_SET']: validationEffect,
			['NOW_TYPEAHEAD_MULTI#INVALID_SET']: validationEffect
		}
	};
};

/**
 * returnFormValidState - returns boolean that indicates whether *EVERY* relevant field
 * is valid or not. All valid states must be truthy for this boolean to be true.
 *
 * Indented to be implemented in a component's `transformState`, so that validation can
 * be re-caculated on each state change.
 */

export const returnFormValidState = ({ behaviors }) => {
	const fieldValidStates = behaviors[BEHAVIOR_NAME];
	for (const fieldKey in fieldValidStates) {
		if (!fieldValidStates[fieldKey]) {
			return false;
		}
	}
	return true;
};
