/**
 * create-context-helpers.js
 *
 * A function that takes in a `config` object with the following structure:
 * 	{
 * 		namespace: <string>,
 * 		keys: [<string>]
 * 	}
 *
 * The `namespace` string refers to the key on `wizardContext` where the data is stored in.
 * The `keys` array refer to the individual keys of that `namespace` object that the view
 * cares about. These `keys` will be applied onto the component's state.
 *
 * And returns two functions:
 *
 * 1. getStateFromContext - accepts `wizardContext` that returns a new state object based on
 *  what is present in `wizardContext`. Intended to be invoked on COMPONENT_BOOTSTRAPPED and
 *  be passed into `updateState`
 * 2. getContextFromState - accepts `state` and `wizardContext` that returns a new *slice* of
 *  context. This is intended to be passed as the optional payload to either
 *  'SN_GUIDED_WIZARD#CONTINUE' or 'SN_GUIDED_WIZARD#PREVIOUS' to update that corresponding
 *  slice of context.
 */

const createNewStateFn = (config, wizardContext) => {
	const { namespace, keys } = config;
	const context = { ...wizardContext[namespace] };
	const newState = {};

	for (let key of keys) {
		if (context.hasOwnProperty(key)) {
			newState[key] = context[key];
		}
	}

	return newState;
};

const createNewContextFn = (config, state, wizardContext) => {
	const { namespace, keys } = config;
	let newContext = { ...wizardContext[namespace] };

	for (let key of keys) {
		if (!state.hasOwnProperty(key)) {
			throw new Error(`Cannot find ${key} on state`);
		}
		newContext[key] = state[key];
	}

	return { [namespace]: newContext };
};

export default config => {
	return {
		getStateFromContext: wizardContext =>
			createNewStateFn(config, wizardContext),
		getContextFromState: (state, wizardContext) =>
			createNewContextFn(config, state, wizardContext)
	};
};
