import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import createFetchTableNamesEffect from './createFetchTableNamesEffect';
import {
	loadingPropertyName,
	BEHAVIOR_NAME,
	TABLES_DATA_PROPERTY,
	TABLES_DATA_IN_SCOPE_PROPERTY,
	TABLES_DATA_OUT_OF_SCOPE_PROPERTY,
	EXTENDABLE_TABLES_DATA_PROPERTY,
	TABLES_DATA_LOADING_PROPERTY,
	EXTENDABLE_TABLES_DATA_LOADING_PROPERTY,
	TABLES_DATA_OPTIONS_PROPERTY,
	EXTENDABLE_FIELD,
	DEFAULT_QUERY_PARAMS,
	TABLES_DATA_BEHAVIOR__FETCH_ALL,
	TABLES_DATA_BEHAVIOR__FETCH_EXTENDABLE,
	TABLES_DATA_BEHAVIOR__FETCH_TABLES,
	TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES,
	TABLES_DATA_BEHAVIOR__FETCH_TABLES_STARTED,
	TABLES_DATA_BEHAVIOR__FETCH_TABLES_SUCCEEDED,
	TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES_SUCCEEDED,
	TABLES_DATA_BEHAVIOR__FETCH_TABLES_FAILED
} from './constants';

import { fetchAllTables, fetchExtendableTables } from './helpers';

export default {
	name: BEHAVIOR_NAME,

	properties: {
		[TABLES_DATA_PROPERTY]: { default: [], selectable: true },
		[TABLES_DATA_LOADING_PROPERTY]: { default: false, selectable: true },
		[EXTENDABLE_TABLES_DATA_PROPERTY]: { default: [], selectable: true },
		[TABLES_DATA_IN_SCOPE_PROPERTY]: { default: [], selectable: true },
		[TABLES_DATA_OUT_OF_SCOPE_PROPERTY]: { default: [], selectable: true },
		[EXTENDABLE_TABLES_DATA_LOADING_PROPERTY]: {
			default: false,
			selectable: true,
			fetchOnBootstrap: false,
			appId: null,
			extendedOnly: false
		},
		[TABLES_DATA_OPTIONS_PROPERTY]: {
			default: { normalizeResult: null, queryParams: {} },
			schema: {
				type: 'object',
				normalizeResult: {},
				queryParams: { type: 'object' },
				fetchOnBootstrap: { type: 'boolean' },
				appId: { type: 'string' },
				extendedOnly: { type: 'boolean' }
			}
		}
	},

	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
			const { [TABLES_DATA_OPTIONS_PROPERTY]: options } = properties;
			if (options.fetchOnBootstrap && options.appId) {
				if (options.extendedOnly) {
					fetchExtendableTables(dispatch, options.appId);
				} else {
					fetchAllTables(dispatch, options.appId);
				}
			}
			if (options.fetchOnBootstrap && !options.appId) {
				console.error(
					'fetchOnBootstrap option requires an appId option to be set'
				);
			}
		},

		[TABLES_DATA_BEHAVIOR__FETCH_ALL]: createFetchTableNamesEffect(
			TABLES_DATA_PROPERTY
		),

		[TABLES_DATA_BEHAVIOR__FETCH_EXTENDABLE]: createFetchTableNamesEffect(
			EXTENDABLE_TABLES_DATA_PROPERTY,
			{
				[EXTENDABLE_FIELD]: true
			}
		),

		[TABLES_DATA_BEHAVIOR__FETCH_TABLES_STARTED]: ({
			action,
			updateProperties
		}) => {
			const { propName } = action.meta;
			updateProperties({ [loadingPropertyName(propName)]: true });
		},

		[TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES]: createHttpEffect(
			'/api/sn_app_eng_studio/generic/template-tables',
			{
				method: 'GET',
				batch: false,
				queryParams: Object.keys(DEFAULT_QUERY_PARAMS),
				startActionType: TABLES_DATA_BEHAVIOR__FETCH_TABLES_STARTED,
				successActionType: TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES_SUCCEEDED,
				errorActionType: TABLES_DATA_BEHAVIOR__FETCH_TABLES_FAILED
			}
		),

		[TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES_SUCCEEDED]: ({
			properties,
			action,
			updateProperties,
			dispatch
		}) => {
			const {
				meta: {
					propName,
					request: { params }
				}
			} = action;
			const options = properties[TABLES_DATA_OPTIONS_PROPERTY];
			let { result } = action.payload;

			if (typeof options.normalizeResult === 'function') {
				result = options.normalizeResult(result) || result;
			}

			updateProperties({
				[TABLES_DATA_IN_SCOPE_PROPERTY]: result
			});

			const queryParams = {
				...params,
				only_in_scope: false,
				only_out_of_scope: true
			};

			dispatch(TABLES_DATA_BEHAVIOR__FETCH_TABLES, queryParams, { propName });
		},

		[TABLES_DATA_BEHAVIOR__FETCH_TABLES]: createHttpEffect(
			'/api/sn_app_eng_studio/generic/template-tables',
			{
				method: 'GET',
				batch: false,
				queryParams: Object.keys(DEFAULT_QUERY_PARAMS),
				startActionType: TABLES_DATA_BEHAVIOR__FETCH_TABLES_STARTED,
				successActionType: TABLES_DATA_BEHAVIOR__FETCH_TABLES_SUCCEEDED,
				errorActionType: TABLES_DATA_BEHAVIOR__FETCH_TABLES_FAILED
			}
		),

		[TABLES_DATA_BEHAVIOR__FETCH_TABLES_SUCCEEDED]: ({
			properties,
			action,
			updateProperties
		}) => {
			const options = properties[TABLES_DATA_OPTIONS_PROPERTY];
			const { propName } = action.meta;
			let { result } = action.payload;

			if (typeof options.normalizeResult === 'function') {
				result = options.normalizeResult(result, propName) || result;
			}

			const scopeResults = properties[TABLES_DATA_IN_SCOPE_PROPERTY] || [];

			updateProperties({
				[propName]: scopeResults.concat(result),
				[TABLES_DATA_OUT_OF_SCOPE_PROPERTY]: result,
				[loadingPropertyName(propName)]: false
			});
		},

		[TABLES_DATA_BEHAVIOR__FETCH_TABLES_FAILED]: ({
			action,
			updateProperties
		}) => {
			const { propName } = action.meta;

			updateProperties({
				[loadingPropertyName(propName)]: false
			});
		}
	}
};
