const BASE = 'SN_GUIDED_WIZARD#';
export const SN_GW__CONTEXT_SET = `${BASE}CONTEXT_SET`;
export const SN_GW__CONTINUE = `${BASE}CONTINUE`;
export const SN_GW__BACK = `${BASE}PREVIOUS`;
export const SN_GW_PAGE_CHANGED = `${BASE}PAGE_CHANGED`;

export const SN_GW__WIZARD_COMPLETE = `${BASE}COMPLETE`;
export const SN_GW__WIZARD_CANCEL = `${BASE}CANCEL`;

export const getCurrentPage = wizardContext => {
	if (typeof wizardContext === 'undefined') {
		return 'span';
	}
	if (!wizardContext.meta.currentPage) {
		return 'span';
	}
	return wizardContext.meta.currentPage;
};
