import { actionTypes } from '@servicenow/ui-core';

export const BEHAVIOR_NAME = 'STEPPER_BEHAVIOR';
const BASE = `${BEHAVIOR_NAME}#`;
export const STEPPER_INIT = `${BASE}STEPPER_INIT`;
export const STEPPER_PAGE_SET = `${BASE}PAGE_SET`;
export const STEPPER_COMPLETE_ALL = `${BASE}COMPLETE_ALL`;

const stepperBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		config: { default: {} },
		stepperItems: {
			default: [],
			selectable: true
		},
		currentStep: {
			default: '',
			selectable: true
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			action: { payload },
			dispatch
		}) => {
			dispatch(STEPPER_INIT, {
				config: payload.options.config
			});
		},
		[STEPPER_INIT]: ({ action: { payload }, updateProperties }) => {
			updateProperties({
				stepperItems: Object.keys(payload.config).map(key => ({
					id: key,
					label: payload.config[key],
					disabled: true
				}))
			});
		},
		[STEPPER_PAGE_SET]: ({
			properties: { stepperItems },
			action: {
				payload: { page }
			},
			updateProperties
		}) => {
			if (!page) {
				return;
			}

			const currentIndex = stepperItems.findIndex(item => item.id === page);

			if (currentIndex < 0) {
				throw new Error(`Could not find page component with ${page} tag`);
			}

			// eslint-disable-next-line
			const newItems = stepperItems.map(({ disabled, ...item }, index) => {
				if (index === currentIndex) {
					return { ...item, progress: 'partial', disabled: false };
				}
				if (index < currentIndex) {
					return { ...item, progress: 'done', disabled: true };
				}
				return { ...item, progress: 'none', disabled: true };
			});

			updateProperties({
				stepperItems: newItems,
				currentStep: page // Since the page tag is used as the `item.id`
			});
		},
		[STEPPER_COMPLETE_ALL]: ({
			properties: { stepperItems },
			updateProperties
		}) => {
			updateProperties({
				stepperItems: stepperItems.map(item => ({
					...item,
					progress: 'done',
					disabled: false
				}))
			});
		}
	}
	// dispatches: {
	// 	// payload: { page: 'tagNameOfCurrentPage }
	// 	// Sets the active page stepper progress to "partial"
	// 	// Sets previous pages progress to "done"
	// 	// Sets future pages progress to "none"
	// 	'STEPPER_BEHAVIOR#PAGE_SET': {},

	// 	// Sets all items progress to "done"
	// 	'STEPPER_BEHAVIOR#COMPLETE_ALL': {}
	// }
};

export default stepperBehavior;
