import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-guided-wizard-page-header.scss';

createCustomElement('sn-guided-wizard-page-header', {
	view: ({
		properties: {
			runningHeaderText,
			headerText,
			subheaderText,
			paddingLeft,
			paddingRight
		}
	}) => {
		return (
			<div
				className="gw-page-header-row"
				style={{
					paddingLeft,
					paddingRight
				}}>
				{runningHeaderText && (
					<h2 className="running-header-text">{runningHeaderText}</h2>
				)}
				{headerText && <h1 className="header-text">{headerText}</h1>}
				{subheaderText && (
					<span className="subheader-text">{subheaderText}</span>
				)}
			</div>
		);
	},
	properties: {
		runningHeaderText: { default: '' },
		headerText: { default: '' },
		subheaderText: { default: '' },
		paddingRight: { default: '0' },
		paddingLeft: { default: '0' }
	},
	dispatches: {},
	styles
});
