import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

import {
	BEHAVIOR_NAME,
	USER_DATA_PROPERTY,
	USER_INITIAL_FETCH_PROPERTY,
	USER_DATA_LOADING_PROPERTY,
	USER_DATA_OPTIONS_PROPERTY,
	USER_DATA_BEHAVIOR__FETCH_USER,
	USER_DATA_BEHAVIOR__USER_FETCH_STARTED,
	USER_DATA_BEHAVIOR__USER_FETCH_SUCCEEDED,
	USER_DATA_BEHAVIOR__USER_FETCH_FAILED,
	USER_DATA_BEHAVIOR__USER_FETCH_ERROR,
	USER_DATA_BEHAVIOR__UPDATE_PREF,
	USER_DATA_BEHAVIOR__PREF_UPDATE_STARTED,
	USER_DATA_BEHAVIOR__PREF_UPDATE_SUCCEEDED,
	USER_DATA_BEHAVIOR__PREF_UPDATE_FAILED,
	USER_DATA_BEHAVIOR__PREF_UPDATE_ERROR
} from './constants';

const fetchUserQuery = `
query($userPreferences: [String]!) {
	GlideDomain_Query {
		user {
			sys_id
			firstName
			lastName
			fullName
			roles
			avatar
			preferences(name: $userPreferences) {	
				name	
				value	
			}
		}
	}
}
`;

// Note that there are two main error cases -
// 1. one where the GQL request fails outright (e.g. USER_DATA_BEHAVIOR__USER_FETCH_FAILED)
// 2. the other where the request succeeds but the response body contains an error (e.g. USER_DATA_BEHAVIOR__USER_FETCH_ERROR)
const fetchUserEffect = createGraphQLEffect(fetchUserQuery, {
	variableList: ['userPreferences'],
	startActionType: USER_DATA_BEHAVIOR__USER_FETCH_STARTED,
	successActionType: USER_DATA_BEHAVIOR__USER_FETCH_SUCCEEDED,
	errorActionType: USER_DATA_BEHAVIOR__USER_FETCH_FAILED
});

const userPrefUpdateQuery = `
mutation($name: String!, $value: String!) {
	GlideDomain_Mutation {	
		user {	
			setPreference(name: $name, value: $value) {	
				name	
				value	
			}	
		}	
	  }
}
`;

const userPrefUpdateEffect = createGraphQLEffect(userPrefUpdateQuery, {
	variableList: ['name', 'value'],
	startActionType: USER_DATA_BEHAVIOR__PREF_UPDATE_STARTED,
	successActionType: USER_DATA_BEHAVIOR__PREF_UPDATE_SUCCEEDED,
	errorActionType: USER_DATA_BEHAVIOR__PREF_UPDATE_FAILED
});

export const updateUserPrefArray = (staleUserData, payload) => {
	const updatedPref = payload.data.GlideDomain_Mutation.user.setPreference;
	let newUserPrefs = [...staleUserData.preferences];
	const prefIdx = newUserPrefs.findIndex(
		pref => pref.name === updatedPref.name
	);
	if (prefIdx > -1) {
		newUserPrefs[prefIdx] = updatedPref;
	} else {
		newUserPrefs = [...newUserPrefs, updatedPref];
	}
	return { ...staleUserData, preferences: newUserPrefs };
};

const userDataBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[USER_DATA_PROPERTY]: {
			default: {},
			selectable: true
		},
		[USER_INITIAL_FETCH_PROPERTY]: {
			default: false,
			selectable: true
		},
		[USER_DATA_LOADING_PROPERTY]: {
			default: false,
			selectable: true
		},
		[USER_DATA_OPTIONS_PROPERTY]: {
			default: { userPreferences: [] },
			schema: {
				type: 'object',
				userPreferences: { type: 'array', items: { type: 'string' } }
			}
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch, updateProperties }) => {
			const userPreferences =
				properties[USER_DATA_OPTIONS_PROPERTY].userPreferences || [];
			dispatch(USER_DATA_BEHAVIOR__FETCH_USER, { userPreferences });
			updateProperties({ [USER_DATA_LOADING_PROPERTY]: true });
		},
		[USER_DATA_BEHAVIOR__FETCH_USER]: fetchUserEffect,
		[USER_DATA_BEHAVIOR__USER_FETCH_SUCCEEDED]: ({
			updateProperties,
			dispatch,
			action: { payload }
		}) => {
			const { errors = [] } = payload;
			if (errors.length) {
				console.error('GlideDomain_Query.user has returned errors: ', errors);
				dispatch(USER_DATA_BEHAVIOR__USER_FETCH_ERROR, errors);
				return;
			}
			const { user } = payload.data.GlideDomain_Query;
			updateProperties({
				[USER_DATA_PROPERTY]: user,
				[USER_INITIAL_FETCH_PROPERTY]: true,
				[USER_DATA_LOADING_PROPERTY]: false
			});
		},
		[USER_DATA_BEHAVIOR__UPDATE_PREF]: userPrefUpdateEffect,
		[USER_DATA_BEHAVIOR__PREF_UPDATE_SUCCEEDED]: ({
			properties,
			action: { payload },
			dispatch,
			updateProperties
		}) => {
			const { errors = [] } = payload;

			if (errors.length) {
				console.error(
					'GlideDomain_Mutation.user.setPreference returned errors: ',
					errors
				);
				dispatch(USER_DATA_BEHAVIOR__PREF_UPDATE_ERROR, errors);
				return;
			}
			const staleUserData = properties[USER_DATA_PROPERTY];
			const updatedUserData = updateUserPrefArray(staleUserData, payload);

			updateProperties({ [USER_DATA_PROPERTY]: updatedUserData });
		},
		[USER_DATA_BEHAVIOR__USER_FETCH_FAILED]: ({ updateProperties }) => {
			// TODO: Throw developer-targeted errors here?
			updateProperties({ [USER_DATA_LOADING_PROPERTY]: false });
		},
		[USER_DATA_BEHAVIOR__USER_FETCH_ERROR]: ({ updateProperties }) => {
			updateProperties({ [USER_DATA_LOADING_PROPERTY]: false });
		}
	}
};

export default userDataBehavior;
