import {
	TABLES_DATA_BEHAVIOR__FETCH_ALL,
	TABLES_DATA_BEHAVIOR__FETCH_EXTENDABLE
} from './constants';

export function fetchAllTables(dispatch, sysparm_transaction_scope) {
	dispatch(TABLES_DATA_BEHAVIOR__FETCH_ALL, { sysparm_transaction_scope });
}

export function fetchExtendableTables(dispatch, sysparm_transaction_scope) {
	dispatch(TABLES_DATA_BEHAVIOR__FETCH_EXTENDABLE, {
		sysparm_transaction_scope
	});
}
