import { serializeSNName, commonSNPrefixName } from './sn-naming-helpers';

// Transforms a string into a valid ServiceNow identifier.
export default function getSNName(prefix, name) {
	if (!name) {
		return '';
	}

	const appName = commonSNPrefixName(prefix, name);
	return serializeSNName(appName, 18);
}
