import {
	BEHAVIOR_NAME,
	DEFAULT_QUERY_PARAMS,
	TABLES_DATA_OPTIONS_PROPERTY,
	TABLES_DATA_BEHAVIOR__FETCH_TABLES,
	TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES
} from './constants';

/**
 * @private
 * @param {string} propName Property name to update on success
 * @param {object} [extendedQueryParams] Query parameters to extend DEFAULT_QUERY_PARAMETERS
 * @param {object} coeffects
 */
export function fetchTableNamesEffect(
	propName,
	extendedQueryParams = {},
	coeffects
) {
	const { action, dispatch, properties } = coeffects;
	const customParams =
		properties[TABLES_DATA_OPTIONS_PROPERTY].queryParams || {};
	const { sysparm_transaction_scope } = action.payload;

	if (
		typeof sysparm_transaction_scope !== 'string' ||
		sysparm_transaction_scope.length < 1
	) {
		throw new Error(
			`[${BEHAVIOR_NAME}] '${
				action.type
			}' requires that you pass the current app 'sysparm_transaction_scope' in the payload`
		);
	}

	const queryParams = {
		...DEFAULT_QUERY_PARAMS,
		...extendedQueryParams,
		...customParams,
		sysparm_transaction_scope
	};

	let actionType;

	if (!queryParams.only_out_of_scope) {
		actionType = TABLES_DATA_BEHAVIOR__FETCH_SCOPE_TABLES;
	} else {
		actionType = TABLES_DATA_BEHAVIOR__FETCH_TABLES;
	}

	dispatch(actionType, queryParams, { propName });
}

/**
 * Create an effect that specifies a property to update, and append query parameters to, a request to fetch table names
 *
 * @public
 * @param {string} propName Property name to update on success
 * @param {{ is_extendable: boolean }} [extendedQueryParams] Query parameters to extend DEFAULT_QUERY_PARAMETERS
 */
export default function createFetchTableNamesEffect(
	propName,
	extendedQueryParams
) {
	if (typeof propName !== 'string') {
		throw new Error(
			`[${BEHAVIOR_NAME}] You must specify a property to update on fetch success`
		);
	}

	return {
		effect: fetchTableNamesEffect,
		args: [propName, extendedQueryParams]
	};
}
