import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-button';
import '../sn-guided-wizard-page-header';
import {
	SN_GW_PAGE__HEADER_SLOT,
	SN_GW_PAGE__FOOTER_SLOT,
	SN_GW_PAGE__LOADER_SLOT
} from './constants';
import styles from './sn-guided-wizard-page.scss';

createCustomElement('sn-guided-wizard-page', {
	view: (
		{
			properties: {
				onPrimaryClick,
				primaryDisabled,
				primaryLabel,
				onSecondaryClick,
				secondaryDisabled,
				secondaryLabel,
				onPrevious,
				previousDisabled,
				onClose,
				hideCloseButton,
				runningHeaderText,
				headerText,
				subheaderText,
				maxWidth,
				loading,
				loadingContent
			}
		},
		{ dispatch }
	) => {
		const onCloseFunc = () =>
			onClose && typeof onClose === 'function'
				? onClose()
				: dispatch('SN_GUIDED_WIZARD#CANCEL');
		return (
			<div className="gw-page-container">
				{typeof onPrevious === 'function' ? (
					<now-button-iconic
						bare
						className="gw-page-previous-button"
						configAria={{ 'aria-label': 'previous' }}
						on-click={() => !previousDisabled && onPrevious()}
						disabled={previousDisabled}
						icon="arrow-left-fill"
						size="lg"
					/>
				) : (
					<span />
				)}
				{!hideCloseButton ? (
					<now-button-iconic
						bare
						className="gw-page-close-button"
						configAria={{ 'aria-label': 'close' }}
						icon="close-fill"
						on-click={onCloseFunc}
						size="lg"
					/>
				) : (
					<span />
				)}

				<div className="gw-page" style={{ maxWidth: maxWidth }}>
					<div className="gw-page-main-content">
						<slot name={SN_GW_PAGE__HEADER_SLOT}>
							<div className="gw-page-header-row">
								<sn-guided-wizard-page-header
									running-header-text={
										loading
											? loadingContent.runningHeaderText || runningHeaderText
											: runningHeaderText
									}
									header-text={
										loading
											? loadingContent.headerText || headerText
											: headerText
									}
									subheader-text={
										loading
											? loadingContent.subheaderText || subheaderText
											: subheaderText
									}
								/>
							</div>
						</slot>

						{/** Default slot for body content */}
						<div className="gw-page-default-slot-container">
							{loading ? (
								<slot name={SN_GW_PAGE__LOADER_SLOT}>
									<div className="loading-content">
										<now-progress-bar
											size="sm"
											className="now-m-block-end--xl"
											config-aria={{
												'aria-label': t('Loading...')
											}}
										/>
										{loadingContent.image ? (
											<img src={loadingContent.image} alt="" />
										) : null}
									</div>
								</slot>
							) : (
								<slot />
							)}
						</div>
						{loading ? (
							<div />
						) : (
							<footer className="gw-page-footer-content">
								<slot name={SN_GW_PAGE__FOOTER_SLOT} />
								{typeof onSecondaryClick === 'function' ? (
									<now-button
										className="gw-page-secondary-button"
										on-click={() => !secondaryDisabled && onSecondaryClick()}
										disabled={secondaryDisabled}
										label={secondaryLabel}
										variant="tertiary"
									/>
								) : null}
								{typeof onPrimaryClick === 'function' ? (
									<now-button
										className="gw-page-primary-button"
										on-click={() => !primaryDisabled && onPrimaryClick()}
										disabled={primaryDisabled}
										label={primaryLabel}
										variant="primary"
									/>
								) : null}
							</footer>
						)}
					</div>
				</div>
			</div>
		);
	},
	properties: {
		name: { default: '' },

		onPrimaryClick: { default: null },
		primaryDisabled: { default: false },
		primaryLabel: { default: t('Continue') },

		onSecondaryClick: { default: null },
		secondaryDisabled: { default: false },
		secondaryLabel: { default: t('Cancel') },

		onPrevious: { default: null },
		previousDisabled: { default: false },

		onClose: { default: null },
		hideCloseButton: { default: false },

		runningHeaderText: { default: '' },
		headerText: { default: '' },
		subheaderText: { default: '' },
		maxWidth: { default: '1024px' },

		loading: { default: false, schema: { type: 'boolean' } },
		loadingContent: {
			default: {
				runningHeaderText: { default: '', schema: { type: 'string' } },
				headerText: { default: '', schema: { type: 'string' } },
				subheaderText: { default: '', schema: { type: 'string' } },
				image: { default: '', schema: { type: 'string' } }
			},
			schema: { type: 'object' }
		}
	},
	dispatches: {},
	styles
});
