import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	BEHAVIOR_NAME,
	ROLES_DATA_PROPERTY,
	ROLES_DATA_IN_SCOPE_PROPERTY,
	ROLES_DATA_OUT_OF_SCOPE_PROPERTY,
	ROLES_DATA_LOADING_PROPERTY,
	ROLES_DATA_OPTIONS_PROPERTY,
	DEFAULT_FETCH_QUERY_PARAMS,
	ROLES_DATA_BEHAVIOR__FETCH,
	ROLES_DATA_BEHAVIOR__FETCH_ROLES,
	ROLES_DATA_BEHAVIOR__FETCH_SCOPE_ROLES,
	ROLES_DATA_BEHAVIOR__FETCH_ROLES_STARTED,
	ROLES_DATA_BEHAVIOR__FETCH_ROLES_SUCCEEDED,
	ROLES_DATA_BEHAVIOR__FETCH_ROLES_FAILED,
	ROLES_DATA_BEHAVIOR__FETCH_SCOPE_ROLES_SUCCEEDED
} from './constants';

import { fetchRoles } from './helpers';

export default {
	name: BEHAVIOR_NAME,

	properties: {
		[ROLES_DATA_PROPERTY]: { default: [], selectable: true },
		[ROLES_DATA_IN_SCOPE_PROPERTY]: { default: [], selectable: true },
		[ROLES_DATA_OUT_OF_SCOPE_PROPERTY]: { default: [], selectable: true },
		[ROLES_DATA_LOADING_PROPERTY]: { default: false, selectable: true },
		[ROLES_DATA_OPTIONS_PROPERTY]: {
			default: {
				normalizeResult: null,
				queryParams: {},
				fetchOnBootstrap: false,
				appId: null
			},
			schema: {
				type: 'object',
				normalizeResult: {},
				queryParams: { type: 'object' },
				fetchOnBootstrap: { type: 'boolean' },
				appId: { type: 'string' }
			}
		}
	},

	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
			const { [ROLES_DATA_OPTIONS_PROPERTY]: options } = properties;
			if (options.fetchOnBootstrap && options.appId) {
				fetchRoles(dispatch, options.appId);
			}
			if (options.fetchOnBootstrap && !options.appId) {
				console.error(
					'fetchOnBootstrap option requires an appId option to be set'
				);
			}
		},
		[ROLES_DATA_BEHAVIOR__FETCH]: ({
			properties,
			action: {
				payload: { sysparm_transaction_scope },
				type
			},
			dispatch
		}) => {
			const customParams =
				properties[ROLES_DATA_OPTIONS_PROPERTY].queryParams || {};

			if (
				typeof sysparm_transaction_scope !== 'string' ||
				sysparm_transaction_scope.length < 1
			) {
				throw new Error(
					`[${BEHAVIOR_NAME}] '${type}' requires that you pass the current app 'sysparm_transaction_scope' in the payload`
				);
			}

			const queryParams = {
				...DEFAULT_FETCH_QUERY_PARAMS,
				...customParams,
				sysparm_transaction_scope
			};

			let actionType;

			// If no sysparm_query specified, request in-scope roles and out-of-scope roles seperately by default
			if (!customParams.sysparm_query) {
				queryParams.sysparm_query = `sys_scope=${sysparm_transaction_scope}^${
					DEFAULT_FETCH_QUERY_PARAMS.sysparm_query
				}`;
				actionType = ROLES_DATA_BEHAVIOR__FETCH_SCOPE_ROLES;
			}
			// Alternatively, if sysparm_query is specified but no sys_scope is given request in-scope/out-of-scope separately
			// with the included sysparm_query
			else if (!customParams.sysparm_query.includes('sys_scope')) {
				queryParams.sysparm_query = `${
					customParams.sysparm_query
				}^sys_scope=${sysparm_transaction_scope}`;
				actionType = ROLES_DATA_BEHAVIOR__FETCH_SCOPE_ROLES;
			}
			// If sysparm_query includes sys_scope, fetch as-is
			else {
				actionType = ROLES_DATA_BEHAVIOR__FETCH_ROLES;
			}

			dispatch(actionType, queryParams);
		},

		[ROLES_DATA_BEHAVIOR__FETCH_ROLES_STARTED]: ({ updateProperties }) => {
			updateProperties({ [ROLES_DATA_LOADING_PROPERTY]: true });
		},

		[ROLES_DATA_BEHAVIOR__FETCH_SCOPE_ROLES]: createHttpEffect(
			'/api/now/table/sys_user_role',
			{
				method: 'GET',
				batch: false,
				queryParams: Object.keys(DEFAULT_FETCH_QUERY_PARAMS),
				startActionType: ROLES_DATA_BEHAVIOR__FETCH_ROLES_STARTED,
				successActionType: ROLES_DATA_BEHAVIOR__FETCH_SCOPE_ROLES_SUCCEEDED,
				errorActionType: ROLES_DATA_BEHAVIOR__FETCH_ROLES_FAILED
			}
		),

		[ROLES_DATA_BEHAVIOR__FETCH_ROLES]: createHttpEffect(
			'/api/now/table/sys_user_role',
			{
				method: 'GET',
				batch: false,
				queryParams: Object.keys(DEFAULT_FETCH_QUERY_PARAMS),
				startActionType: ROLES_DATA_BEHAVIOR__FETCH_ROLES_STARTED,
				successActionType: ROLES_DATA_BEHAVIOR__FETCH_ROLES_SUCCEEDED,
				errorActionType: ROLES_DATA_BEHAVIOR__FETCH_ROLES_FAILED
			}
		),

		[ROLES_DATA_BEHAVIOR__FETCH_SCOPE_ROLES_SUCCEEDED]: ({
			properties,
			action,
			updateProperties,
			dispatch
		}) => {
			const {
				meta: {
					request: { params }
				}
			} = action;
			const options = properties[ROLES_DATA_OPTIONS_PROPERTY];
			let { result } = action.payload;

			if (typeof options.normalizeResult === 'function') {
				result = options.normalizeResult(result) || result;
			}

			updateProperties({
				[ROLES_DATA_IN_SCOPE_PROPERTY]: result
			});

			const queryParams = {
				...params,
				sysparm_query: params.sysparm_query.replace('sys_scope=', 'sys_scope!=')
			};
			dispatch(ROLES_DATA_BEHAVIOR__FETCH_ROLES, queryParams);
		},

		[ROLES_DATA_BEHAVIOR__FETCH_ROLES_SUCCEEDED]: ({
			properties,
			action,
			updateProperties
		}) => {
			const options = properties[ROLES_DATA_OPTIONS_PROPERTY];
			let { result } = action.payload;

			if (typeof options.normalizeResult === 'function') {
				result = options.normalizeResult(result) || result;
			}
			const scopedResults = properties[ROLES_DATA_IN_SCOPE_PROPERTY] || [];

			updateProperties({
				[ROLES_DATA_PROPERTY]: scopedResults.concat(result),
				[ROLES_DATA_OUT_OF_SCOPE_PROPERTY]: result,
				[ROLES_DATA_LOADING_PROPERTY]: false
			});
		},

		[ROLES_DATA_BEHAVIOR__FETCH_ROLES_FAILED]: ({ updateProperties }) => {
			// TODO: Add developer-focused error here?
			updateProperties({
				[ROLES_DATA_LOADING_PROPERTY]: false
			});
		}
	}
};
