import { RUN_TEMPLATE, BEHAVIOR_NAME } from './constants';

/**
 * @param {function} dispatch
 * @param {string} template_sys_id The template ID you wish to run
 * @param {object} variables The variables object that corresponds to the template
 * @param {object} [options]
 * @param {string} [options.sysparm_transaction_scope] The application scope under which the template should run
 * @param {string} [options.parent_template_instance_id] The parent instance ID of the parent template if the running template is a subtemplate
 */

export function runTemplate(
	dispatch,
	template_sys_id,
	variables,
	options = {}
) {
	dispatch(RUN_TEMPLATE, {
		...options,
		template_sys_id,
		variables
	});
}

export const isInProgress = state => {
	if (!state.behaviors[BEHAVIOR_NAME]) {
		throw new Error('Please add the run-template behavior');
	}
	return state.behaviors[BEHAVIOR_NAME].inProgress;
};
