export const SN_GLOBAL_SCOPE = 'global';
export const SN_GLOBAL_SCOPE_PREFIX = 'u_';
export const USER_ROLE_MAINT = 'maint';

export function commonSNPrefixName(prefix, name) {
	return (prefix ? prefix + '_' : '') + name;
}

export function serializeSNName(name, length) {
	return name
		.toLowerCase()
		.replace(/[^a-zA-Z0-9]/g, '_')
		.replace(/_+/g, '_')
		.slice(0, length)
		.replace(/_$/g, '');
}

export function validSNCharacters(name) {
	return !name || /^[a-z0-9_]*$/.test(name);
}

export function validPrefix(name, prefix) {
	return name.startsWith(prefix || '');
}

const ROLE_MAP = {
	[USER_ROLE_MAINT]: (scope, label) => {
		if (scope === SN_GLOBAL_SCOPE) {
			return label;
		}
		return commonSNPrefixName(scope, label);
	},
	default(scope, label) {
		if (scope === SN_GLOBAL_SCOPE) {
			return SN_GLOBAL_SCOPE_PREFIX + label;
		}
		return commonSNPrefixName(scope, label);
	}
};

export function getSNTableName(scope, label, isMaint) {
	if (!label) {
		return '';
	}

	// Mayble pass the role name(s) in instead of assuming 'maint' in the future
	const nameFn = ROLE_MAP[isMaint ? USER_ROLE_MAINT : 'default'];
	const tableName = nameFn(scope, label);

	return serializeSNName(tableName, 80);
}

export function isValidSNTableName(tableName, tableLabel, scope, isMaint) {
	const globalScope = scope === SN_GLOBAL_SCOPE;
	if (globalScope) {
		if (isMaint) {
			return tableName.startsWith(tableLabel);
		}
		return tableName.startsWith(SN_GLOBAL_SCOPE_PREFIX + tableLabel);
	}
	return tableName.startsWith(scope) && tableName !== scope;
}
